@import '../../styles/index.scss';

footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 100%;
    height: 209px;
    gap: 30px;
    text-align: center;
    font-size: clamp(0.75rem, 0.3166rem + 1.849vw, 1.5rem);
    background-color: black;
    color: $text-color-white;
    margin-top: auto;

    img {
       width: 122px;
       height: auto;
    }
}