@import '../../styles/settings.scss';

.home-container {
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    flex-grow: 1;

    .gallery-layout{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        border-radius: 25px;
        margin-bottom: 60px;
        background-color: rgba(255, 255, 255, 0);
        transition: .55s ease;

        @media screen and (min-width: 381px) {
            padding: 20px;
            background-color: $layout-background-gallery;
        }

        .gallery {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            width: 100%;
            height: 100%;
            gap: 20px;
            transition: .5s;

            @media screen and (min-width: 920px) {
                justify-content: center;
                margin: 30px;
                gap: 60px;
            }
        }
    }
}