.about-container {
    display: flex;
    flex-direction: column;
    gap: 30px;
    flex-grow: 1;

    .dropdown-info {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        gap: 20px;

        @media screen and (min-width: 700px) {
            gap: 31px;
        }
    }
}
